import { firestore } from '../../firebase'
import { convertDate, getTime } from './../../components/modules/DateFormat';


const state = {
    pasients_selected:[],
    pasients:[],
    pasientByID:{},
    total_pasients:null,
}

const getters = {
    pasients(state){
        let hasValue = state.pasients.filter(item => item.name !== undefined)
        if(hasValue){
            return state.pasients;
        } else null
        
    },
    getTotalPasients(state){
        return state.total_pasients;
    },
    getPasientByID(state){
        return state.pasientByID
    },
}

const mutations = {
    addPasient(state, pasient){
        state.pasients.push({
            name:pasient.name,
            address:pasient.address,
            latest_checkup:pasient.latest_checkup
        })            
    },

    updatePasient(state, payload){
        state.pasients.find(item => {
            if(item.id == payload.id_pasient) {
                item.name = payload.name,
                item.age = payload.age,
                item.address = payload.address 
            }
        })
    },

    deletePasient(state, index) {
        // const index = state.pasients.find(item => item.id === id_pasient)
        state.pasients.splice(index, 1)
    },
  
    deletePasientsSelected (state, pasients_selected) {
        for(let i = 0; i < pasients_selected.length; i++){
            const index = state.pasients.findIndex(pasients_selected.id_selects[i]);
            //onst index = state.pasients.find(item => item.id === pasients_selected.id_selects[i])
            state.pasients.splice(index, 1);
        }
    },

    setListPasients(state, data) {
        state.pasients = data
    },

    setTotalPasients(state, data){
        state.total_pasients = data
    },

    setPasientByID(state, data){
        state.pasientByID = data
    }
}

const actions = {
    retrievePasients(context, email) {
        let tempPasients = []
        // firestore.collection('pasients').where('dibuat_oleh', '==', email).orderBy('tanggal_periksa_terakhir', 'desc').get()
        firestore.collection('pasients').where('dibuat_oleh', '==', email).orderBy('tanggal_periksa_terakhir', 'desc').get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                if(doc !== undefined){
                    const pasient = {
                        id: doc.id,
                        name: doc.data().nama,
                        age: doc.data().umur,
                        address: doc.data().alamat,
                        latest_checkup: convertDate(new Date(doc.data().tanggal_periksa_terakhir.seconds * 1000)),
                        latest_checkup_time: getTime(doc.data().tanggal_periksa_terakhir.seconds * 1000)
                    }
                    tempPasients.push(pasient)
                }
            })
            context.commit('setListPasients', tempPasients)
            console.log(tempPasients)
        }).catch(err => {
            console.log('error : ', err)
        })
    },

    retrievePasientByID(context, idPasient){
        firestore.collection('pasients').doc(idPasient).get()
        .then(querySnapshot => {
            const pasient = {
                name:querySnapshot.data().nama,
                age:querySnapshot.data().umur,
                address:querySnapshot.data().alamat,
                latest_checkup:convertDate(new Date(querySnapshot.data().tanggal_periksa_terakhir.seconds * 1000))
            }
            console.log(querySnapshot.data())
            context.commit('setPasientByID', pasient)
        }).catch(err => {
            console.log(err)
        })
    },
    
    retrieveTotalPasients(context, email){
        firestore.collection('pasients').where('dibuat_oleh', '==', email).get()
            .then(querySnapshot => {
                console.log(querySnapshot.size)
                context.commit('setTotalPasients', querySnapshot.size)
            }).catch(err =>{
                console.log(err)
            })
    },

    addPasient(context, pasient){
        let dataUser = JSON.parse(localStorage.getItem('store'))
        let data = {
            nama:pasient.name,
            umur:pasient.age,
            alamat:pasient.address,
            tanggal_periksa_terakhir: new Date(),
            tangga_buat: new Date(),
            dibuat_oleh: dataUser.users.user.email
        }
        firestore.collection('pasients').add(data)
        .then(ref => {
            context.commit('addPasient', {
                id:ref.id,
                data
            })
        }).catch(err => {
            console.log("error :", err)
        })  
    },

    updatePasient(context, payload){
        // let data = {
        //     nama: payload.pasient.name,
        //     umur: payload.pasient.age,
        //     alamat:payload.pasient.address
        // }
        firestore.collection('pasients').doc(payload.id_pasient).update({
            nama:payload.name,
            umur:payload.age,
            alamat:payload.address,
        }).then( ref => {
            context.commit('updatePasient', payload)
        }).catch(err => {
            console.log(err)
        })
    },

    deletePasient(context, payload) {
        firestore.collection('pasients').doc(payload.id_pasient).delete()
            .then(()=> {
                context.commit('deletePasient', payload.index)
            }).catch(err => {
                console.log('error :', err)
            })
    },

    deleteSelected (context, pasients) {
        if(pasients.length && pasients.length > 0){
            for (let i = 0; i < pasients.length; i++){
                firestore.collection('pasients').doc(pasients.id_selects[i]).delete()
                .then( () => {
                    context.commit('deletePasientsSelected', pasients);
                }).catch(err => {
                    console.log(err)
                })
            }
        }
    },
}

export default {
    namespaced:true,
    state,
    getters,
    mutations,
    actions
}
