import { firestore } from '../../firebase'
import { convertDate, getTime } from './../../components/modules/DateFormat';

const state = {
    recap:[],
    recaps_pasient:[],
    pasient_id:'',
    recaps_by_id:{},
}

const getters = {
    getRecapsPasient: state => {
        return state.recaps_pasient
    },

    getPasientId: state => {
        return state.pasient_id
    }
}

const mutations = {
    addRecap(state, payload){
        state.recaps_pasient.push({
            diagnosa:payload.recap.diagnosa,
            therapy:payload.recap.therapy
        })
    },

    setRecapsPasient(state, data){
        state.recaps_pasient = data
    },

    setPasientId(state, data){
        state.pasient_id = data
    },

    updateRecap(state, payload){
        state.recaps_pasient.find(item => {
            if(item.id == payload.id_recap) {
                item.diagnosa = payload.diagnosa,
                item.therapy = payload.therapy
            }
        })
    },

    deleteRecap(state, index) {
        state.recaps_pasient.splice(index, 1)
    },
}

const actions = {
    retrieveRecapsPasient(context, id_pasient){
        firestore.collection('pasients').doc(id_pasient).collection('data_medis').orderBy('tanggal_periksa', 'desc').get()
        .then(querySnapshot => {
            let tempRecaps = []
            querySnapshot.forEach(doc => {
                const data_medis = {
                    id: doc.id,
                    diagnosa:doc.data().diagnosa,
                    date_checkup: convertDate(new Date(doc.data().tanggal_periksa.seconds * 1000)),
                    time_checkup: getTime(doc.data().tanggal_periksa.seconds * 1000),
                    therapy: doc.data().terapi
                }   
                tempRecaps.push(data_medis) 
                console.log(data_medis)
                firestore.collection('pasients').doc(id_pasient).update({
                    latest_checkup:convertDate(new Date(doc.data().tanggal_periksa.seconds * 1000)),
                    latest_checkup_time: getTime(doc.data().tanggal_periksa.seconds * 1000)
                })
            })
            context.commit('setRecapsPasient', tempRecaps)
        }).catch(err => {
            console.log(err)
        })
    },
    
    addRecap(context, payload){
        let id_pasient = payload.id_pasient
        let data = {
            tanggal_periksa: new Date(),
            diagnosa:payload.recap.diagnosa,
            terapi: payload.recap.therapy
        }
        console.log(id_pasient)
        console.log(data)
        firestore.collection('pasients').doc(id_pasient).collection('data_medis').add(data)
        .then(ref => {
            context.commit('addRecap', {
                // id_pasient:id_pasient,
                id:ref.id,
                data
            })
        }).catch(err => [
            console.log("error : ", err)
        ])
    },

    updateRecap(context, payload){
        firestore.collection('pasients').doc(payload.id_pasient).collection('data_medis').doc(payload.id_recap).update({
            diagnosa:payload.diagnosa,
            terapi:payload.therapy,
        }).then( ref => {
            context.commit('updateRecap', payload)
        }).catch(err => {
            console.log(err)
        })
    },

    deleteRecap(context, payload){
        firestore.collection('pasients').doc(payload.id_pasient).collection('data_medis').doc(payload.id_recap).delete()
        .then(()=> {
            context.commit('deleteRecap', payload.index)
        }).catch(err => {
            console.log('error :', err)
        })
    },

    emptyCacheRecaps(context){
        context.commit('setRecapsPasient', [])
    }
}

export default {
    namespaced:true,
    state,
    getters,
    mutations,
    actions
}