import { fireauth } from '../../firebase'

const state = {
    user:null,
    isLogin:false,
    userState:null
}

const getters = {
    getIsLogin(state){
        return state.isLogin
    },
    getUser(state){
        if(state.isLogin) return state.user
    },
}

const mutations = {
    setIsLogin(state, payload){
        state.isLogin = payload
    },
    setUser(state, payload){
        state.user = payload
    },
}

const actions = {
    signUpUser({commit}, payload){
        fireauth.createUserWithEmailAndPassword(payload.email, payload.password)
        .then( user => {
            const newUser = {
                id: user.user.uid,
                username: payload.email,
                email: payload.email,
            }
            // console.log(user.user,id)
            commit('setUser', newUser)
        }).catch(err => {
            console.log(err)
        })
    },
    signInUser({commit}, payload){
        fireauth.signInWithEmailAndPassword(payload.email, payload.password)
        .then( user => {
            let isUser = {
                id: user.user.uid,
                username: user.user.displayName || user.user.email,
                email: user.user.email,
            }
            console.log(isUser)
            console.log(user)
            commit('setUser', isUser)
            commit('setIsLogin', user.operationType == 'signIn')
        }).catch(err => {
            console.log(err)
        })
    },
    signOut({commit}, payload){
        commit('setUser', null)
        commit('setIsLogin', payload.isLogin)
        localStorage.clear()
    },
    editUser({commit}, payload){
       let isUser = fireauth.currentUser
       console.log(isUser)
       if(isUser != null){
           isUser.updateProfile({
               id:payload.id,
               displayName:payload.username,
               email:payload.email
           }).then(()=> {
               commit('setUser', payload)
           }).catch(err => {
               console.log(err)
           })
       }
    }  
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}